import axios from 'axios'
import router from '@/router'
import { Message } from 'element-ui'
import { mapMutations } from 'vuex'

const methods = mapMutations(['setLogin', 'setUserInfo', 'setLoginDialog'])

// 
let rootUrl = window.localStorage.rootUrl || ''

if (window.localStorage.rootUrl != null && window.localStorage.rootUrl !== undefined) {
    rootUrl = window.localStorage.rootUrl
}

// 
let instance

const api = {
    rootUrl: rootUrl
}

// 
const reLoadInstance = function () {
    if (window.localStorage.rootUrl != null && window.localStorage.rootUrl !== undefined) {
        rootUrl = window.localStorage.rootUrl
    }
    window.console.log('', rootUrl, {
        __userId: window.localStorage.getItem('userId'),
        __token: window.localStorage.getItem('token')
    })
    instance = axios.create({
        time: 1000 * 60,
        withCredentials: true,
        baseURL: rootUrl,
        adapter: '',
        headers: {
            __userId: window.localStorage.getItem('userId'),
            __token: window.localStorage.getItem('token')
        }
    })
    instance.defaults.withCredentials = true
    instance.defaults.validateStatus = function () {
        return true
    }
    // 
    instance.interceptors.request.use(function (config) {
        // 
        return config
    }, function (error) {
        // 
        Message.error({ message: '!' })
        return Promise.reject(error)
    })
    // 
    instance.interceptors.response.use(function (response) {
        const status = response.status
        // 
        if (status === 200) {
            return Promise.resolve(response)
        } else if (status === 301) {
            Message.error({ message: '!' })
            // router.replace({
            //     path: 'login'
            // })
            return false
        } else if (status === 401) {
            Message.error({ message: '!' })
            console.log('[]401', status, process.env.NODE_ENV)
            window.localStorage.removeItem('isLogin')
            window.localStorage.removeItem('token')
            window.localStorage.removeItem('userInfo')
            window.localStorage.removeItem('token')
            window.localStorage.removeItem('userId')
            window.localStorage.removeItem('LOGIN_RESULT')
            methods.setLogin(false)
            methods.setUserInfo()
            if (process.env.NODE_ENV === 'development') {
                router.replace({
                    path: 'login'
                })
            } else {
                if ((typeof process !== 'undefined' && Object.prototype.toString.call(process) === '[object process]')) {
                    this.$msg.success('')
                    router.replace({
                        path: 'login'
                    })
                } else if (typeof XMLHttpRequest !== 'undefined') {
                    // web 
                    window.location.href = '/login.html'
                }
            }
            return false
        } else {
            return Promise.reject(response)
        }
    }, function (error) {
        // 
        Message.error({ message: '!' })
        return Promise.reject(error)
    })
    const ajaxMethod = ['get', 'post']
    ajaxMethod.forEach(method => {
        // 
        api[method] = function (uri, data, config) {
            return new Promise(function (resolve, reject) {
                instance.post('/api/music/getWyyApi', {
                    url: uri,
                    method: method,
                    data: data
                }, config)
                .then(response => {
                    if (response.status === 200) {
                        resolve(response)
                    }
                })
                .catch(error => {
                    reject(error)
                })
            })
        }
    })
    api.rootUrl = rootUrl
    api.instance = instance
}

// 
reLoadInstance()
api.reLoadInstance = reLoadInstance
api.instance = instance

export default api
